main{
  height: 100vh;
  width: 100%;
  margin: 0 auto;
  position: relative;
}
.flex-box > div{
  width: 50%;
  color: #fff;
  height: 100%;
}
h2{
  margin-top: 0;
}
.step-box{
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.title-page{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.title-page h1{
  margin-left: -50px;
  max-width: 300px;
  text-align: center;
  font-family: 'Permanent Marker', cursive;
  font-size: 70px;
  line-height: 1.1;
  color: #fff;
}
.title-page h1 span{
  padding-left: 60px;
}
.step-box > div{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  position: relative;
}
.curtain{
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #bad5bf;
  width: 50%;
  z-index: 10;
  height: 100%;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
.curtain.left-pos{
  right: 50%;
}
.curtain.away-pos{
  right: -50%;
}
.BurgersPreviewItem{
  margin: 100px auto 0;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
  top: 100px;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  left: 0; 
  right: 0;
}
.BurgersPreviewItem p{
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  text-transform: capitalize;
}
.BurgersPreviewItem.active{
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0)
}
.BurgersPreviewItem.left{
  -webkit-transform: translateX(1500px);
      -ms-transform: translateX(1500px);
          transform: translateX(1500px);
}
.BurgersPreviewItem.right{
  -webkit-transform: translateX(-1500px);
      -ms-transform: translateX(-1500px);
          transform: translateX(-1500px);
}
.step-one{
  background-color: #34495e;
}
.step-two{
  background-color: #e74c3c;
}
.step-three{
  background-color: #34495e;
  width: 100%!important;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
  transition: 300ms;
}
.step-three .check:after{
  background-color: #34495e;
}
.by{
  position: absolute;
  bottom: 0;
  left: 10px;
  font-size: 8px;
  color:#fff;
}
.error-notification{
  position:absolute;
  right:0;
  left:0;
  margin:auto;
  color:#ec0000;
}
/* ANIMATIONS */
.fade-enter{
  opacity: 0;
}
.fade-enter-active{
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  opacity: 1;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
.fade-exit{
  opacity: 1;
}
.fade-exit-active{
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  opacity: 0;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
  transition: 300ms;
}
/* FADE LEFT */
.fade-left-enter{
  opacity: 0;
  -webkit-transform: translateX(-100px);
      -ms-transform: translateX(-100px);
          transform: translateX(-100px);
}
.fade-left-enter-active{
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
.fade-left-exit{
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}
.fade-left-exit-active{
  opacity: 0;
  -webkit-transform: translateX(-100px);
      -ms-transform: translateX(-100px);
          transform: translateX(-100px);
  -webkit-transition: 300ms;
  -o-transition: 300ms;
  transition: 300ms;
}
/* FADE DOWN */
.fade-down-enter{
  opacity: 0;
  -webkit-transform: translateY(100px);
      -ms-transform: translateY(100px);
          transform: translateY(100px);
}
.fade-down-enter-active{
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
.fade-down-exit{
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}
.fade-down-exit-active{
  opacity: 0;
  -webkit-transform: translateY(100px);
      -ms-transform: translateY(100px);
          transform: translateY(100px);
  -webkit-transition: 300ms;
  -o-transition: 300ms;
  transition: 300ms;
}
/* SLIDE UP */
.slide-up-enter{
  top:100%;
}
.slide-up-enter-active{
  top:0;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
.slide-up-enter-done{
  top: 0;
}
.slide-up-exit{
  top:0;
}
.slide-up-exit-active{
  top:100%;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
/* SLIDE RIGHT */
.slide-right-enter{
  -webkit-transform: translateX(400px);
      -ms-transform: translateX(400px);
          transform: translateX(400px);
}
.slide-right-enter-active{
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
.slide-right-enter-done{
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}
.slide-right-exit{
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}
.slide-right-exit-active{
  -webkit-transform: translateX(400px);
      -ms-transform: translateX(400px);
          transform: translateX(400px);
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
/* CURTAIN OPEN */
.curtain-open-enter{
  width:100%;
}
.curtain-open-enter-active{
  width:100%;
}
.curtain-open-enter-done{
  width:100%;
}
.curtain-open-exit{
  width:0;
}
.curtain-open-exit-active{
  width:100%;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}
@media (max-width:768px){
  h3{
    margin: 10px 0!important;
  }
  .curtain{
    display: none;
  }
  .step-one .BurgersPreview .BurgersPreviewItem{
    top: 60px;
    margin: 0 auto;
  }
  .step-one .BurgersPreview .BurgersPreviewItem .Burger p{
    font-size: 12px;
    margin: 10px auto;
  }
  .step-one .BurgersPreview .Burger{
    margin: 0;
    width: 250px!important;
    height: 150px!important;
  }
  .BurgersList ul{
    display: none;
  }
  .step-one .controls{
    position: absolute;
    bottom: 0;
    padding-bottom: 30px;
    margin: 0 auto 10px;
    left: 0;
    right: 0;
    /* margin: 380px auto 0; */
  }
  .BurgersPreviewItem p:first-child{
    display: none;
  }
  .step-one .controls .close-btn{
    position: absolute;
    right: -20px;
    top: 0;
  }
  .step-one .controls h2{
    display: none;
  }
  .step-one .controls .next-step-btn, .step-one .controls .prev-step-btn{
    position: absolute;
    bottom: 0;
  }
  .step-one .controls .next-step-btn{
    right: 0;
  }
  .step-one .controls .prev-step-btn{
    left: 0;
  }
  .step-box > div{
    position: absolute;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
  }
  .step-box > div.current{
    right: 0;
  }
  .btn{
    font-size: 16px;
  }
  .curtain.left-pos{
    right: 0;
    top: -50%;
  }
  .loginModal{
    width: 100%;
  }
}
@media (max-width:500px){
  .curtain .BurgersPreviewItem .Burger{
    margin: 10px;
    height: 180px;
    width: 330px;
  }
  .curtain .BurgersPreviewItem{
    top: 20px;
  }
  .curtain .BurgersPreviewItem p{
    font-size: 12px;
    max-width: 280px;
  }
}


