#userProfile .sidebar{
    margin: 50px auto;
    width: 40%;
    word-wrap: break-word;
}
#userProfile .flex-box{
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
#userProfile .options{
    width: 55%;
    margin: 50px auto;
}
#userProfile .options span{
    color: #333;
    display: block;
    margin-bottom: 5px;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
    cursor: pointer;
}
#userProfile .options span:hover{
    color: #ff8800;
}


