.form{
    display: block;
    margin: 0 auto;
}
.formChanger{
    cursor: pointer;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
}
.formChanger:hover{
    color: #ff8800;
}
.error-message{
    color:#ec0000;
}


