#logo{
	margin:0;
	padding:10px 30px;
    color:#fff;
    position: absolute;
    height: 50px;
    top: 0;
    left: 0;
    z-index: 15;
}
.wrapper{
    max-width: 1180px;
    width: 100%;
    margin: 0 auto;
}
.user{
    cursor: pointer;
    position: absolute;
    padding:10px 30px;
    right: 0;
    top: 10px;
    z-index: 15;
    color: #fff;
}
.user span{
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
}
header .user svg{
    z-index: 2;
    position: relative;
}
header .user svg path{
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
}

.loginModal{
    -webkit-perspective: 1000px;
            perspective: 1000px;
    background-color: #e67e22;
    color: #fff;
    position: absolute;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    overflow: hidden;
    right: 0;
    z-index: 11;
    -webkit-transition: 500ms;
    -o-transition: 500ms;
    transition: 500ms;
    width: 50%;
    height: 0%;
    text-align: center;
}
.loginModal .Login{
    width: 45%;
    margin: auto;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
    z-index: 2;
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
    position: relative;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.loginModal .Login .check::after{
    background-color: #e67e22;
}
.loginModal input{
    -webkit-box-shadow: none;
            box-shadow: none;
    border-radius: 0;
}
.loginModal.active{
    height: 100%;
}
.closeModalBtn{
    position: absolute;
    width: 100%;
    bottom: 0;
    height: 50px;
    background-color: rgba(0, 0, 0, .3);
    border: none;
    background-image: url('../../assets/img/up.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 40px;
    cursor: pointer;
}


